:root{
  --theme-color: rgb(255, 115, 0);
  --theme-highlight: #0049ff
}
html{
  scroll-behavior: smooth;
}
body{
  overflow-x: hidden;
}
.App {
  max-width: 1300px;
  margin: auto;
  padding-bottom: 50px;
  padding: 0 40px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
main > section{
  margin-top: 30px;
}

.App-header {
  margin: auto;
}

a {
  color: inherit;
  text-decoration: none;
}
*{
  box-sizing: border-box;
}
.card{
  padding: 30px;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

/* navbar */
#nav-menu{
  display: flex;
  justify-content: flex-end;
  padding: 20px 10px;
}
#nav-menu ul{
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  align-items: center;
}
#nav-menu ul li{
  text-decoration: none;
  font-weight: 400;
  position: relative;
  margin: 0 15px;
  display: flex;
}
#nav-menu ul :is(.resume){
  padding: 3px 10px;
  border-radius: 15px;
  background-color: var(--theme-color);
  color: white;
  font-weight: bold;
  margin: 0;
  height: 30px;
}

#nav-menu ul :not(.resume) li::after{
  content: "";
  position: absolute;
  height: 2px;
  width: 0%;
  bottom: -5px;
  left: 50%;
  background-color: var(--theme-color);
  transform: translateX(-50%);
  transition: width .2s ease-out;
}
#nav-menu ul :not(.resume) li:hover::after{
  width: 130%;
}
#nav-menu ul :not(.resume) li.selected::after{
  width: 130% !important;
}

/* navbar ended */
main {
  position: relative;
}
main section.intro{
  display: flex;
  min-height: calc(100vmin - 100px);
  align-items: center;
  width: 100%;
  position: relative;
}

main section.intro .blob-animation{
  position: absolute;
  isolation: isolate;
  z-index: -2;
  width: 100%;
  left: 0;
  top: 50%;
  height: calc(100vmin - 100px);
  filter: blur(200px);
  transform: translateX(-50%) translateY(-50%);
  opacity: 1;
  max-height: 500px;
}

main section.intro > div{
  flex: 1;
}
main section.intro .image{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
main section.intro .image svg{
  position: absolute;
  width: 100%;
  left: 0;
  max-height: 600px;
}
main section.intro #pic{
  width: 100%;
  margin-top: 20px;
  max-width: 220px;
  mask-image: linear-gradient(black 70%, transparent 78%);
  filter: sepia(1) contrast(1) hue-rotate(343deg);
}
main section.intro .content{
  width: 50%;
  text-align: left;
  padding: 100px;
  position: relative;
}
main section.intro .content h1{
  font-size: 2.5em;
}
main section.intro .content p{
  color: gray;
  font-size: 1.15em;
}

main section.intro .hire-me-button{
  margin-top: 30px;
  position: relative;
}
main section.intro .hire-me-button button{
  height: 50px;
  width: 130px;
  background-color: var(--theme-highlight);
  color: white;
  font-size: 1.3em;
  border: none;
  border-radius: 30px;
  cursor: pointer;
}
main section.intro .hire-me-button::after{
  content: "";
  position: absolute;
  height: 15px;
  width: 15px;
  background-color: var(--theme-highlight);
  clip-path: polygon(100% 50%, 0 0, 0 100%);
  top: 50%;
  transform: translateY(-50%) translateX(50px);
}
main section.intro .social-accounts{
  display: flex;
  margin-top: 100px;
  gap: 20px;
}
main section.intro .social-icon{
  color: var(--theme-color);
  border: 2px solid var(--theme-color);
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
  transition: all .5s ease-out;
  transform: translate(0);
}
main section.intro .social-icon:hover{
  animation: pop .2s 1 forwards ease-out;
}
@keyframes pop {
  0%{
    transform: translateY(0px);
  }
  30%{
    transform: translateY(1px);
  }
  100%{
    transform: translateY(-5px);
  }
}
.page-title{
  text-align: right;
  position: relative;
}
.page-title.left{
  text-align: left;
}
.page-title::after, .page-title::before{
  content: "";
  position: absolute;
  height: 5px;
  width: 150px;
  background-color: var(--theme-color);
  bottom: -10px;
  right: 0;
  border-radius: 3px;
}
.page-title::before{
  width: 50px;
  bottom: -20px;
}
.page-title.left::after, .page-title.left::before{
  right: auto;
  left: 0;
}
.page-content-about > section{
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  justify-items: center;
  row-gap: 50px;
  padding: 100px 0;
  position: relative;
  overflow:visible;
  align-items: flex-start;
}
.page-content-about > section .about-card{
  width: 80%;
  background-color: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  color: rgb(171, 171, 171);
  font-size: 1.125em;
  line-height: 25px;
}
.page-content-about > section .about-card:nth-child(even){
  margin-top: -80px;
}
.page-content-about > section .about-page-blob{
  position: absolute;
  width: 50%;
  right: 0;
  bottom: 0;
  filter: blur(200px);
  z-index: -2;
  isolation: isolate;
  transform: translateY(30%);
}

.page-content-skill > section{
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
  padding: 30px;
}
.page-content-skill > section .skills-card-img{
  height: 80px;
  width: 80px;
  object-fit: contain;
  padding: 5px;
}
.page-content-skill > section .skills-card{
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  text-align: center;
}

/* product page */

.project-card{
  display: flex;
  position: relative;
  overflow: hidden;
  padding: 30px;
}
.project-card > *{
  position: relative;
}
.project-card .project-card-blob{
  position: absolute;
  width: 50%;
  right: 0;
  bottom: 0;
  transform: translateX(60%) translateY(50%);
}
.project-card:nth-of-type(2) .project-card-blob{
  bottom: auto;
  top: 0;
  transform: translateX(60%) translateY(-60%) rotate(90deg)
}
.project-card img{
  height: 50px;
  margin: 20px;
  margin-left: 0;
}
.project-content{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.project-card .project-details{
  display: flex;
  width: 100%;
}
.project-card .project-details :is(.project-title, .project-description){
  margin: 0;
  text-align: left;
}
.project-card .project-details{
  display: flex;
  gap: 5px;
  flex-direction: column;
}
.project-card .project-details .project-description{
  color: gray;
  max-width: 900px;
  font-size: 1em;
}
.project-info{
  display: flex;
  gap: 30px;
}
.project-info section{
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.project-info section span{
  font-size: .75em;
  font-weight: bold;
}
.project-info section div{
  display: flex;
  gap: 10px;
}
.project-info section svg{
  color: black !important;
  transition: color .2s ease-out;
}
.project-info section svg:hover{
  color: var(--color) !important;
}
.project-deployement svg{
  cursor: pointer;
}
.page-content-project > section{
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.card-section{
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
}
.card-section .project-card:nth-of-type(4n+2) .project-card-blob{
  right: auto;
  top: auto;
  left: 0;
  bottom: 0;
  transform: translateX(-50%) translateY(50%);
}
.card-section .project-card:nth-of-type(4n+3) .project-card-blob{
  right: 0;
  top: 0;
  left: auto;
  bottom: auto;
  transform: translateX(50%) translateY(-50%);
}
.card-section .project-card:nth-of-type(4n+4) .project-card-blob{
  right: auto;
  top: 0;
  left: 0;
  bottom: auto;
  transform: translateX(-50%) translateY(-50%);
}
.card-section .project-card{
  max-width: calc(50% - 20px);
  min-width: calc(50% - 20px);
}
.card-section .project-app{
  display: flex;
}
.card-section .project-app .android-app img{
  border-radius: 10px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.project-game{
  display: flex;
  justify-content: flex-start;
  gap: 40px;
  flex-wrap: wrap;
  padding: 30px;
  padding-top: 0;
}
.project-game-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 10px;
  width: 120px;
  max-width: 120px;
  text-align: center;
  cursor: pointer;
  transition: all .1s ease-in;
}
.project-game-card:hover{
  scale: 1.1;
  transform: translateY(-10px);
}
.project-game-card img{
  width: 60px;
  height: 60px;
  object-fit: contain;
}

.github-content > section{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.github-content > section section{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.contact-details{
  display: flex;
  gap: 30px;
  justify-content: center;
}
.contact-details a{
  display: flex;
  gap: 5px;
  align-items: center;
}